import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import "./styles.css";
import axios from "axios";


function App() {
  const [sensors, setSensors] = useState([]);

  const hook = () => {
    console.log('effect')
    axios
      .get('http://localhost:3001/api')
      .then(response => {
        console.log('promise fulfilled')
        setSensors(response.data)
      })
  }
  
  useEffect(hook, [])


  return (
    <div>
      <h1>Sensors</h1> 
      <div style={{ display: "flex" }}>
      
       
  <svg width="12000" height="550">
  <rect x="0" y="100" width="100" height="0"  />
  <rect x="200" y="100" width="100" height="276" fill="#a29bfe"  />
  <rect x="500" y="355" width="100" height="19.1" fill="#a29bfe" />
  <rect x="800" y="290" width="100" height="86" fill="#a29bfe" />
  <text x="200" y="415" fill="#6c5ce7">Sensor 2 </text>
  <text x="500" y="415" fill="#6c5ce7">Sensor 3</text>
  <text x="800" y="415" fill="#6c5ce7">Sensor 4</text>
  <text x="200" y="75" fill="#6c5ce7">276 </text>
  <text x="500" y="75" fill="#6c5ce7">19.1</text>
  <text x="800" y="75" fill="#6c5ce7">86</text>
</svg>
        
       
      </div>
     
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
